// 
// _nav.scss
// 

.nav-tabs,.nav-pills {
  > li {
      > a {
          color: var(--#{$prefix}body-color);
          font-weight: $font-weight-medium;
          &:hover{
            color: $primary;
          }
      }
  }
}

.nav-pills {
  > a {
      color: var(--#{$prefix}body-color);  
      font-weight: $font-weight-medium;
  }
}


.nav-tabs-custom {
  border-bottom: 2px solid var(--#{$prefix}border-color);

  .nav-item {
    position: relative;
    color: $dark;
    .nav-link {
      border: none;
      // &:hover{
      //   color: $primary !important;
      // }
      &::after {
        content: "";
        background: $primary;
        height: 2px;
        position: absolute;
        width: 100%;
        left: 0;
        bottom: -1px;
        transition: all 250ms ease 0s;
        transform: scale(0);
      }

      &.active{
        color: $primary;
        &:after{
          transform: scale(1);
        }
      }

    }
  }
}
